<template>
	<view class="container">
		<view class="quikmenu">
			<view class="item">
				<view class="label">姓名</view>
				<uni-easyinput type="text" v-model="formData.name" placeholder="请输入您的姓名" class="input" :inputBorder="false" />
			</view>
			<view class="item">
				<view class="label">手机电话</view>
				<uni-easyinput type="text" v-model="formData.tel" placeholder="请输入收件人手机号" class="input" :inputBorder="false"  />
			</view>
			<view class="item" @click="openLocation">
				<view class="label">所在地区</view>
				<uni-easyinput type="text" v-model="formData.address_name" suffixIcon="arrowright" placeholder="请选择所在地区"
					class="input" :inputBorder="false"/>
			</view>
			<view class="item">
				<view class="label">详细地址</view>
				<uni-easyinput type="text" v-model="formData.address" placeholder="街道、楼牌号等" class="input" :inputBorder="false"  />
			</view>		 
		</view>
		<view class="switch-default">
			<view class="item">
				<view class="label">设置默认地址</view>
				<view class="name">提醒：每次下单会默认推荐使用该地址</view>
			</view>
			<switch :checked="isdefault" style="transform: scale(0.8,0.8)" color="#FF0000"
				@change="changeDefault" />
		</view>
		<view class="add" @click="save">保存</view>
	</view>
</template>

<script>
	const app = getApp()
	const key = app.globalData.map; //使用在腾讯位置服务申请的key
	const referer = 'app'; //调用插件的app的名称

	const chooseLocation = requirePlugin('chooseLocation');
	export default {
		data() {
			return {
				formData: {
					uid: 0,
					name: '',
					tel: '',
					address_name: '',
					address: '',
					latitude: '',
					longitude: '',
					isdefault: false
				},
				isdefault: false
			}
		},
		onLoad(option) {
			this.formData.id = option.id
			this.getInfo()
		},
		onShow() {	
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
			this.formData.uid = this.vuex_login
			const location = chooseLocation.getLocation(); // 如果点击确认选点按钮，则返回选点结果对象，否则返回null
			console.log(location)
			if (location) {
				this.formData.address_name = location.province + ' ' + location.city + ' ' + location.district
				this.formData.address = location.address
				this.formData.latitude = location.latitude
				this.formData.longitude = location.longitude
			}
		},
		onUnload() {
			// 页面卸载时设置插件选点数据为null，防止再次进入页面，geLocation返回的是上次选点结果
			chooseLocation.setLocation(null);
		},
		methods: {
			openLocation() {

				wx.navigateTo({
					url: 'plugin://chooseLocation/index?key=' + key + '&referer=' + referer +
						'&location=&category='
				});
			},
			async getInfo() {
				let res = await this.$u.get('/v2/member/address/view', {
					id: this.formData.id
				})
				this.formData.name = res.data.realname
				this.formData.tel = res.data.mobile			 
				this.formData.address_name = res.data.address_name
				this.formData.address = res.data.address_details		 
				this.formData.isdefault = res.data.is_default
				this.isdefault = res.data.is_default == 1 ? true : false
			},
			async save() {
				console.log(this.formData)
				this.formData.uid = this.vuex_user.id
				let bool = Object.values(this.formData).every((item) => item !== "");
				if (!bool) {
					this.$u.toast('有选项没填')
					return
				}
				if(!this.$u.test.mobile(this.formData.tel)){
					this.$u.toast('手机格式不正确')
					return
				}
				let res = await this.$u.post('/v2/member/address/save', this.formData)
				if (res.code == 0) {
					this.$u.toast(res.message)
					return
				}
				uni.navigateTo({
					url: './index'
				})

			},
			showModal() {
				let that = this
				uni.showModal({
					title: '提示',
					content: '确定要删除这个地址吗',
					success: function(res) {
						if (res.confirm) {
							that.del()
						} else if (res.cancel) {
							return
						}
					}
				});
			},
			async del() {
				let res = await this.$u.get('/v2/member/address/del', {
					id: this.formData.id
				})
				if (res.code == 0) {
					this.$u.toast(res.message)
					return
				}
				uni.navigateTo({
					url: './index'
				})
			},
			change(e) {
				console.log(e.detail.value)
				let address_name = [];
				let address_value = [];
				for (let k in e.detail.value) {
					address_name.push(e.detail.value[k].text)
					address_value.push(e.detail.value[k].value)
				}
				this.formData.province_id = address_value[0]
				this.formData.city_id = address_value[1]
				this.formData.area_id = address_value[2]
				this.formData.address_name = address_name.join(' ')
			},
			changeDefault(e) {
				this.formData.isdefault = e.detail.value ? 1 : 0

			}
		}
	}
</script>

<style scoped>
	.container {
		min-height: 100vh;
		background-color: #f2f2f2;
		display: flex;
		flex-direction: column;

	}

	.quikmenu {
		display: flex;
		flex-direction: column;
		background-color: #fff;
		padding: 20rpx 30rpx 0 30rpx;
		margin: 20rpx 0 0 0;
	}

	.quikmenu .item {
		display: flex;
		align-items: center;
		font-size: 28rpx;
		border-bottom: #efefef 1px solid; 
		margin: 10rpx 0;
		padding-bottom: 20rpx;
		color: #666666;
		font-weight: 500;
	}
	.quikmenu .item:last-child{
		border-bottom: none; 
	}
	.quikmenu .item .input {
		flex: 1;
	}

	.quikmenu .item .label {
		margin: 0 30rpx 0 0;
		min-width: 120rpx;
	}
	.switch-default{
		background-color: #fff;
		padding: 20rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 20rpx;
	}
	.switch-default .label{
		font-size: 30rpx;
		font-weight: 500;
		color: #333333;
		margin-bottom: 10rpx;
	}
	.switch-default .name{
		font-size: 24rpx;
		font-weight: 500;
		color: #999999;
	}
	.add {
		margin: 40rpx 20rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		height: 88rpx;
		border-radius: 44rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		width: 690rpx;
	}
</style>